import {
  Box,
  Typography,
  Button,
  Checkbox,
  ButtonGroup,
} from "@mui/material";
import BarChart from "./Charts";
import { BubbleChart, HorizontalChart } from "./Charts";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import KeyboardArrowDownOutlinedIcon from "@mui/icons-material/KeyboardArrowDownOutlined";
import EmailOutlinedIcon from "@mui/icons-material/EmailOutlined";
import ChevronRightIcon from '@mui/icons-material/ChevronRight';

const Feed = (props) => {
  const {state} = props;
  const boxStyle = {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-evenly",
    gap: "40px",
  };

  const ChartContainerStyle = {
    width: "450px",
    height: "250px",
    backgroundColor: "White",
    padding: "10px 10px",
    borderRadius: "5px"
  };

  const iconBarStyle = {
    display: "flex",
    gap: "15px",
  };

  const NavBoxStyle = {
    marginTop:"10px",
    display: "flex",
    flexDirection: "row",
    justifyContent: "flex-end"
  };

  return (
    <Box p={3}>
      {/*Main Heading and buttons */}
      <Box style={{ display: "flex", alignItems: "center", justifyContent: "space-between" }}>
        <Box style={{display: "flex", alignItems: "center"}}>
          <ChevronLeftIcon fontSize="medium" />
          <Typography variant="h4" sx={{ fontSize: "28px" }}>
            Default Playlist of BigFix Environment
          </Typography>
        </Box>

        <Box style={iconBarStyle}>
          <Button variant="contained" sx={{ height: "2%" }}>
            Export in PDF
          </Button>
          <Button variant="outlined" sx={{ height: "2%" }}>
            <EmailOutlinedIcon sx={{ marginRight: "5px" }} />
            Share
          </Button>
        </Box>

      </Box>

      {/*sub section 1 */}
      <Box
        style={{
          backgroundColor: "white",
          marginTop: "20px",
          marginBottom: "20px",
          padding: "10px",
          borderRadius: "10px",
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-between",
        }}
      >
        <Typography>Details</Typography>
        <Typography>
          Analyze main KPIs related to the Linux patching campaign
        </Typography>
        <KeyboardArrowDownOutlinedIcon />
      </Box>

      {/*sub section 2 */}
      <Box
        style={{
          marginTop: "20px",
          marginBottom: "20px",
          padding: "10px",
          borderRadius: "10px",
          display: "flex",
          flexDirection: "row",
          justifyContent: "space-between",
        }}
      >
        <div
          style={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "space-between",
            alignItems: "center",
            gap: "30px",
          }}
        >
          <Typography>4 widgets</Typography>
          <div>
            <Checkbox />
          </div>
        </div>
        <div
          style={{
            marginRight: "10px",
            display: "flex",
            flexDirection: "row",
            alignItems: "center",
            gap: "10px",
          }}
        >
          <Typography>View</Typography>
          <div>
            <ButtonGroup variant="outlined" aria-label="outlined button group">
              <Button variant="contained" sx={{borderRadius: "20px 0px 0px 20px"}}>Slider</Button>
              <Button sx={{borderRadius: "0px 20px 20px 0px"}}>Grid</Button>
            </ButtonGroup>
          </div>
        </div>
      </Box>

      {/*Main body i.e. Charts */}
      <Box style={boxStyle}>
        <div style={ChartContainerStyle}>
          <HorizontalChart state={state} />
        </div>
        <div style={ChartContainerStyle}>
          <BarChart state={state} />
        </div>
        <div style={ChartContainerStyle}>
          <BubbleChart state={state} />
        </div>
      </Box>

      <div style={NavBoxStyle}>
        <ChevronLeftIcon fontSize="large" />
        <ChevronRightIcon fontSize="large" />
      </div>
    </Box>
  );
};

export default Feed;
