export function reducer(state, action) {
  switch (action.type) {
    case ACTIONS.SET_QUERIES_DATA:
      return {
        ...state,
        queryResults: {
          ...state.queryResults,
          [action.payload.queryId]: {
            query: action.payload.query,
            result: action.payload.result,
          },
        },
      }
      case ACTIONS.MODE:
        
        return {
          ...state,               
          mode: action.payload
        }
    default:
      return initialState
  }
}

function App() {
  useEffect(() => {
    dispatch({ type: ACTIONS.MODE, payload:  {palette : {mode : 'dark'}} });
  }, [])
  
  
  const [state, dispatch] = useReducer(reducer, initialState)
  const Theme = { ...state.mode, ...theme };
  console.log(Theme)
    // Function to dispatch an action to update results
    const updateResults = (queryId, query, result) => {
      dispatch({
        type: ACTIONS.SET_QUERIES_DATA,
        payload: { queryId, query, result },
      });
    };
  
    useEffect(() => {
     
      const test = (queryArray) => {
        let gblQueryCnt = 0;
  
        const callback = (result, tmpObj) => {
          // Dispatch an action to update results
          updateResults(`query${tmpObj}`, queryArray[tmpObj-1], result);
        };
  
        for (let i = 0; i < queryArray.length; i++) {
          let q = queryArray[i];
          gblQueryCnt++;   
          asyncQueue(q, callback, gblQueryCnt , 'Tests');
        }
      };
  
      // Example test queries
      test([
        '(name of it, number of relevant fixlets of it) of bes computers',
        '(name of it, number of relevant fixlets of it) of members of bes computer groups whose (name of it = \"Mod 1 Group\")',
        '(id of item 0 of it,  name of item 0 of it,  item 1 of it)  of (( it,  number of computers of results whose(relevant flags of it) of it) of unique values of bes fixlets ) whose ( item 1 of it > 0)'       
      ]);
    }, []); 
  
  return (
    <ThemeProvider theme={Theme}>
    <div className="App">
     <Layout state={state}  dispatch={dispatch}/>
     <Button variant='contained' color='primary'>click here</Button>
    </div>
    </ThemeProvider>
  );
}

