import { createTheme, responsiveFontSizes } from "@mui/material";
import { blue, orange } from "@mui/material/colors";

const theme = createTheme(
    {
        palette:{
            mode: "light",
            primary:{
                main: orange[500],
            }
        },
        
        components :{
            MuiButton:{
                styleOverrides : {
                    root:{
                        borderRadius: 20
                    }
                }
            }
        }
    }
);

export default responsiveFontSizes(theme)
  