import React, {useState, useEffect} from "react";
import { Bar, Bubble } from "react-chartjs-2";


const chartStyle = {
  width: '100%'
};

const labels = ["Jan", "Feb", "Mar", "Apr"]


export default function BarChart(props) {
  const [xAxis, setXAxis] = useState([]);
  const [yAxis, setYAxis] = useState([]);

  const {state} = props;
  useEffect(()=>{
    if(state.queryResults.hasOwnProperty("query2") ){
      console.log("It's coming ", state.queryResults.hasOwnProperty("query2") )
      const tempxAxis = state.queryResults.query2.result.map( (value)=> value[0]);
      setXAxis(tempxAxis)
      const tempyAxis = state.queryResults.query2.result.map( (value)=> value[1]);
      setYAxis(tempyAxis)
    }
  },[state])
  console.log('Bar chart', xAxis, yAxis)
  return (
    <div style={chartStyle}>
      <Bar
        data={{
          labels: xAxis,
          datasets: [
            {
              label: "Computer Group Vulnerabilities",
              data: yAxis,
            },
          ],
        }}
        options={{}}
      />
    </div>
  );
}

export function BubbleChart(props) {
  const [xAxis, setXAxis] = useState([]);
  const [yAxis, setYAxis] = useState([]);

  const { state } = props;

  useEffect(() => {
    if (state.queryResults.hasOwnProperty("query3")) {
      console.log("It's coming ", state.queryResults.hasOwnProperty("query3"));
      const result = state.queryResults.query3.result.slice(0, 10).sort((a, b) => {
        return b[2] - a[2];
      });
      const tempxAxis = result.map((value) => value[0] + " " + value[1]);
      setXAxis(tempxAxis);
      const tempyAxis = result.map((value) => value[2]);
      setYAxis(tempyAxis);
    }
  }, [state]);

  console.log('Bubble Chart', xAxis, yAxis);

  const bubbleData = xAxis.map((xValue, index) => ({
    x: index+1,
    y: yAxis[index],
    r: 10, // Assuming r is always 10
  }));

  return (
    <div style={chartStyle}>
      <Bubble
        data={{
          labels: xAxis, // Use xAxis for labels
          datasets: [
            {
              label: "Fixlet Vulnerabilities",
              data: bubbleData,
              backgroundColor: 'rgba(75,192,192,0.4)',
              borderColor: 'rgba(75,192,192,1)',
            },
          ],
        }}
        options={{
          scales: {
            x: {
              title: {
                display: true,
                text: 'Fixlets',
              },
            },
            y: {
              title: {
                display: true,
                text: 'Relevant Computers',
              },
            },
          },
        }}
      />
    </div>
  );
}


export function HorizontalChart(props) {
  const [xAxis, setXAxis] = useState([]);
  const [yAxis, setYAxis] = useState([]);

  const {state} = props;
  useEffect(()=>{
    if(state.queryResults.hasOwnProperty("query1") ){
      console.log("It's coming ", state.queryResults.hasOwnProperty("query1") )
      const tempxAxis = state.queryResults.query1.result.map( (value)=> value[0]);
      setXAxis(tempxAxis)
      const tempyAxis = state.queryResults.query1.result.map( (value)=> value[1]);
      setYAxis(tempyAxis)
    }
  },[state])
  console.log('Horizontal Chart', xAxis, yAxis)
  return (
    <div style={chartStyle}>
      <Bar
        data={{
          labels: xAxis,
          datasets: [
            {
              label: "Computer Vulnerabilities",
              data: yAxis,
            },
          ],
        }}
        options={{
          indexAxis: "y"
        }}
      />
    </div>
  );
}


